import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { BehaviorSubject, Observable, of, throwError } from 'rxjs';
import { retry, catchError, tap } from 'rxjs/operators';
import { Artist } from '../models/artist';

@Injectable({
  providedIn: 'root'
})
export class ArtistService {
  private ARTIST = new BehaviorSubject<Artist[]>([]);

  constructor(
    private http: HttpClient
    ) { }

    apiURL = 'https://api.deezer.com/album/302127'

    httpOptions = {
      headers: new HttpHeaders({ 'Content-Type': 'application/json' })
    };

    get artists() {
      return this.ARTIST.asObservable();
    }

    getArtists(): Observable<Artist[]> {
      return this.http.get<Artist[]>('https://api.deezer.com/album/302127')
      .pipe(
        tap(artists => console.log('fetched artists')),
        catchError(this.handleError)
      );
    }

    getArtist(id): Observable<Artist>{
      const url = `${this.apiURL}/${id}`;
      return this.http.get<Artist>(url)
      .pipe(retry(1), catchError(this.handleError))
    }

    artistSearch(term: string): Observable<Artist>{
      if (!term.trim) {
        return of();
      }
      return this.http.get<Artist>(`${this.apiURL}/?name=${term}`)
      .pipe(retry(1), catchError(this.handleError))
    }

    handleError(error) {
      let errorMessage = '';
      if(error.error instanceof ErrorEvent) {
        // Get client-side error
        errorMessage = error.error.message;
      } else {
        // Get server-side error
        errorMessage = `Error Code: ${error.status}\nMessage: ${error.message}`;
      }
      window.alert(errorMessage);
      return throwError(errorMessage);
   }

}
